// оформлення контейнерів для сторінок
.container {

    padding-left: 15px;
    padding-right: 15px;

    margin-left: auto;
    margin-right: auto;

    @include mobile {
        width: 480px;
    }

    @include tablet {
        width: 768px;
    }

    @include desktop {
        width: 1200px;
    }
}